@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-4xs-3: 8.3px;
  --font-size-7xl-4: 26.4px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;

  /* Colors */
  --color-gray-100: #0d1c18;
  --color-gray-200: #0c1c18;
  --color-gray-300: #030a08;
  --color-white: #fff;
  --color-orangered: #ff3d00;
  --color-darkslategray-100: #1c2d15;
  --color-darkslategray-200: #142c26;
  --color-gainsboro: #d9d9d9;
  --color-lavender: #cfe8ff;

  /* Gaps */
  --gap-8xl: 27px;
  --gap-4xs-5: 8.5px;
  --gap-5xs-4: 7.4px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-smi-7: 12.7px;
  --padding-16xl-4: 35.4px;
  --padding-xs-1: 11.1px;
  --padding-mini-8: 14.8px;

  /* border radiuses */
  --br-5xs: 8px;
  --br-43xl-9: 62.9px;
  --br-5xs-4: 7.4px;
  --br-5xs-5: 7.5px;
  --br-3xs-1: 9.1px;
}
